.venue-section {
	position: relative;
	padding: 5rem 0;

	@include max-md {
		padding: 3rem 0;
	}

	.arch {
		position: absolute;
		top: 0;
		left: 50%;
		width: 100%;
		max-height: 80%;
		transform: translate(-50%, 0);
		overflow: hidden;

		.pillar {
			position: absolute;
			top: 35%;
			width: 20%;

			&-left {
				left: 0;
			}

			&-right {
				right: 0;
			}
		}
	}

	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 35%;
		transform: translate(-30%, 30%) scaleX(-1);

		.orn-2 {
			position: absolute;
			bottom: -5%;
			right: 40%;
			width: 80%;
			transform: rotate(-10deg) scaleX(-1);
		}

		.orn-3 {
			position: absolute;
			bottom: 50%;
			right: 20%;
			width: 60%;
			transform: rotate(-10deg) scaleX(-1);

			.orn-4 {
				position: absolute;
				bottom: 60%;
				left: 20%;
				width: 100%;
				transform: rotate(-10deg) scaleX(-1);
			}
		}
	}

	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 35%;
		transform: translate(15%, 50%) rotate(-15deg);
	}

	.orn-6 {
		position: absolute;
		bottom: 0;
		right: -5%;
		width: 25%;
		transform: rotate(25deg) scaleX(-1);
	}

	.orn-7 {
		position: absolute;
		right: -5%;
		bottom: 3rem;
		width: 35%;
		transform: rotate(-60deg) scaleX(-1);
	}

	.orn-8 {
		position: absolute;
		bottom: 5rem;
		right: 0;
		width: 30%;
		transform: rotate(10deg) scaleX(-1);
	}

	.venue-wrapper {
		position: relative;
		z-index: 3;

		.venue-description {
			position: relative;
			margin: 0 auto 5rem;
			z-index: 2;
			text-align: center;

			@include max-lg {
				margin-bottom: 3rem;
			}
		}

		.venue-content {
			position: relative;

			.ribbon-venue {
				position: relative;
				margin-bottom: 1rem;

				svg {
					width: 40px;
					height: auto;

					path {
						stroke: var(--color-dark);
					}
				}

				.st0 {
					fill: var(--color-secondary);
				}

				.st1 {
					fill: none;
					stroke: var(--color-secondary);
					stroke-width: 19;
					stroke-linecap: round;
					stroke-miterlimit: 10;
					transform-origin: 50% 75%;
				}
			}

			.card {
				background-color: transparent;
				text-align: center;
				border: none;

				.card-body {
					position: relative;
					padding: 0;

					h4 {
						padding: 0 0 1rem;
					}

					hr {
						display: block;
						width: 10%;
						height: 2px;
						margin: 0 auto 1rem;
					}

					.event-description {
						position: relative;
						margin-bottom: 2rem;

						p {
							margin-bottom: 0;
						}
					}

					.event-name {
						position: relative;
						width: 100%;
						padding: 8rem 2rem 3rem;

						&-body {
							position: relative;
							z-index: 5;
						}

						.date {
							position: relative;
							width: 100%;
							display: flex;
							flex-direction: column;
							justify-content: center;
							align-items: center;

							h5 {
								font-family: $font-secondary;
								font-weight: 700;
								margin-bottom: 0;
								font-size: 36px;
								padding: .5rem 0;
							}
						}

						h4 {
							font-family: $font-secondary;
						}

						p {
							margin-bottom: 0;
							font-weight: 600;
						}
					}

					.event-place {
						position: relative;

						&-body {
							position: relative;
							background-color: var(--color-primary);
							color: var(--color-light);
							mask: var(--mask);
							padding: 4rem 1rem 2.25rem;
						}

						.widget-elements {
							position: absolute;
							bottom: 0;
							left: 50%;
							width: 100%;
							transform: translate(-50%, 50%);
						}
					}
				}
			}
		}
	}

	.prokes-wrapper {
		margin: 3rem 0 0;
		position: relative;
		padding: 2rem;
		text-align: center;
		z-index: 2;

		.title-section {
			&::before {
				all: unset;
			}
		}

		@include max-md {
			padding: 2rem 0;
		}

		* {
			position: relative;
		}

		.prokes-wrap {
			display: flex;
			gap: 0.75rem;
			justify-content: center;
			align-items: flex-start;
			flex-wrap: wrap;
			text-align: center;

			.prokes {
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: flex-start;
				width: 132px;

				@include max-md {
					width: 120px;
				}

				@include max-xs {
					width: 45%;
				}
			}
		}
	}

	.row-venue {
		row-gap: 3rem;
	}

	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}

		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}

		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}

		40% {
			opacity: 1;
		}

		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}

		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}

		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}

		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}

		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}